/**
 * Code Execution Hook
 *
 * Handles code submission, job status polling via SSE, and execution state management.
 */

import { useState } from 'react';
import * as actions from '../actions';

export interface UseCodeExecutionResult {
  output: string[];
  isExecuting: boolean;
  execute: (code: string, language: string, filename: string) => Promise<void>;
  setOutput: React.Dispatch<React.SetStateAction<string[]>>;
  clearOutput: () => void;
}

export function useCodeExecution(): UseCodeExecutionResult {
  const [output, setOutput] = useState<string[]>([]);
  const [isExecuting, setIsExecuting] = useState(false);

  /**
   * Poll job status using Server-Sent Events (SSE)
   */
  const pollJobStatus = async (job_id: string): Promise<void> => {
    return new Promise<void>((resolve) => {
      // Create EventSource connection to SSE endpoint
      const eventSource = new EventSource(`/api/result/${job_id}`);

      // Timeout after 60 seconds
      const timeout = setTimeout(() => {
        eventSource.close();
        setOutput(prev => [...prev, "Job monitoring timed out"]);
        setIsExecuting(false);
        resolve();
      }, 60000);

      // Handle incoming messages
      eventSource.onmessage = (event) => {
        try {
          const data = JSON.parse(event.data);

          // Handle errors
          if (data.error) {
            setOutput(prev => [...prev, `Error: ${data.error}`]);
            eventSource.close();
            clearTimeout(timeout);
            setIsExecuting(false);
            resolve();
            return;
          }

          const status = data.status;

          // Update output based on status
          switch (status) {
            case 'completed':
              try {
                const resultObj = typeof data.result === 'string'
                  ? JSON.parse(data.result)
                  : data.result;

                setOutput(prev => [
                  ...prev.filter(msg => !msg.startsWith('Job ') && !msg.startsWith('Compiling')),
                  ...(resultObj.stdout ? [`Output:\n${resultObj.stdout}`] : []),
                  ...(resultObj.stderr ? [`Error:\n${resultObj.stderr}`] : []),
                  `Execution time: ${resultObj.execution_time?.toFixed(3)}s`
                ]);
              } catch (parseErr) {
                console.error("Failed to parse result:", parseErr);
                setOutput(prev => [
                  ...prev.filter(msg => !msg.startsWith('Job ')),
                  "Error parsing result"
                ]);
              }
              eventSource.close();
              clearTimeout(timeout);
              setIsExecuting(false);
              resolve();
              break;

            case 'failed':
              const errorMsg = data.error || data.result?.stderr || "Job failed";
              setOutput(prev => [
                ...prev.filter(msg => !msg.startsWith('Job ')),
                `Error: ${errorMsg}`
              ]);
              eventSource.close();
              clearTimeout(timeout);
              setIsExecuting(false);
              resolve();
              break;

            case 'queued':
            case 'processing':
              setOutput(prev => [
                ...prev.filter(msg => !msg.startsWith('Job ')),
                `Job ${status}...`
              ]);
              break;
          }
        } catch (err) {
          console.error("Error parsing SSE message:", err);
        }
      };

      // Handle connection errors
      eventSource.onerror = (error) => {
        console.error("SSE error:", error);
        setOutput(prev => [...prev, "Connection error - please try again"]);
        eventSource.close();
        clearTimeout(timeout);
        setIsExecuting(false);
        resolve();
      };
    });
  };

  /**
   * Execute code with the given language
   */
  const execute = async (code: string, language: string, filename: string): Promise<void> => {
    if (!code.trim()) {
      setOutput(["Please enter some code"]);
      return;
    }

    setIsExecuting(true);
    setOutput(["Compiling code..."]);

    try {
      const response = await actions.executeCode(code, language, filename);

      if (!response?.job_id) {
        throw new Error("Failed to start job");
      }

      await pollJobStatus(response.job_id);

    } catch (err: any) {
      setOutput(prev => [
        ...prev,
        "Execution failed",
        err.message || "Unknown error occurred"
      ]);
    } finally {
      setIsExecuting(false);
    }
  };

  /**
   * Clear all output
   */
  const clearOutput = () => {
    setOutput([]);
  };

  return {
    output,
    isExecuting,
    execute,
    setOutput,
    clearOutput
  };
}
